import { useState } from "react";
import LoginBar from "../components/LoginBar";
import LoginModal from "../components/LoginModal"; // LoginModal import 
import "../styles/intro.css";

export default function Intro() {
  const [showLoginModal, setShowLoginModal] = useState(false); // LoginModal   

  const handleGetStartedClick = () => {
    setShowLoginModal(true); //    LoginModal 
  };

  const handleCloseModal = () => {
    setShowLoginModal(false); //      
  };

  return (
    <div>
      <LoginBar></LoginBar>
      <div className="main-container">
        <div className="main-box">
          <h1 className="main-title">TABA? !</h1>
          <button className="main-button" onClick={handleGetStartedClick}>
            <span className="main-button-title">Get started</span>
          </button>
        </div>
      </div>
      {showLoginModal && (
        <div className="login-modal-container" onClick={handleCloseModal}>
          <div className="login-modal" onClick={(e) => e.stopPropagation()}>
            <LoginModal />
          </div>
        </div>
      )}
    </div>
  );
}
